import React, {useState, useEffect} from "react"
import { useAnimation, motion, AnimatePresence } from "framer-motion";
import { useInView } from "react-intersection-observer";
import Introduction from "./components/Introduction"
import Projects from "./components/Projects"
import Skills from "./components/Skills"
import About from "./components/About"
import LandingPage from "./components/LandingPage"
import {Collapse, Box} from '@chakra-ui/react'

export default function App() {

  const [show, setShow] = useState(false)
  const [y, setY] = useState(window.scrollY);

  useEffect(() => {
    
    window.addEventListener("wheel", (e) => {
      setShow(true)
    });

    window.addEventListener("scroll", (e) => {
      if (show){
        window.scrollTo(0, 0)
      }
      setShow(true)
      
    });

    return () => { // return a cleanup function to unregister our function since its gonna run multiple times
      window.removeEventListener("wheel", (e) => setShow(true));
      window.removeEventListener("scroll", (e) => {setShow(true); window.scrollTo(0, 0)});
    };

  }, []);


  const squareVariants = {
    visible: { opacity: 1, y:0, transition: { duration: 1 } },
    hidden: { opacity: 0, y:100 }
  };
  
  function Square(props) {
    const controls = useAnimation();
    const [ref, inView] = useInView();
    useEffect(() => {
      if (inView) {
        controls.start("visible");
      }
    }, [controls, inView]);
    return (
      <AnimatePresence>
        <motion.div
          ref={ref}
          animate={controls}
          initial="hidden"
          variants={squareVariants}
        > {props.children} </motion.div>
      </AnimatePresence>
    );

  }

  /*
  1. Introduction
  2. Skills
  3. Projects
  4. Awards and Certifications
  */
  return (
    <>
    <Box maxW='full' bg='#121212'>

      <Collapse in={!show}>
        <LandingPage show={show} setShow = {setShow} />
      </Collapse>
 

      
      <Collapse in={show}>
          <Square>
            <Introduction />
          </Square>
          <Square> 
            <About/> 
          </Square>
          <Square> 
            <Skills />
          </Square>
          <Square>
            <Projects /> 
          </Square>


      </Collapse>
    </Box>
    </>
  )
}
